import React from 'react'
import emailjs from 'emailjs-com';
import './contacto.css'
import hubicacionImg from './img/contact_img/hubicacion.PNG'

function Contacto() {
    function sendEmail(e) {
        e.preventDefault();
    
        emailjs.sendForm('service_6n826wl', 'template_thf3izk', e.target, 'user_Inb4pnOSNCR7kkMPeI7v5')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
          
          e.target.reset();
    
      }

     const confirmation=()=>{
         alert('Gracias! Tu mensaje ha sido enviado')
     }



    return (
        <div class="contact" id='opacar3'>
<div className='margin'>
    <br/><br/>
    <h2>Dejanos tu <span className='purple'>mensaje</span></h2>
</div>
            <div class="contact_flex">
                
               <div class="contact_left">
                <br/>
                    <h2>Informacin de contcto</h2><br/>
                    <div className='line'></div>
                    <br/>
                    <p>Matriz (Offset): Calle Martin Portela No. 2 Esq. Ave. Tapia Gamez, Hermosillo, Son. 
Tel. (662) 2103513 y 2612072
</p><br/>
                    <p>Express (Digital): Calle Gral Pia 243A-S, Ley 57, 83100 Hermosillo, Son.
 Tel. (662) 210 17 99, 267 17 09 y 302 32 93
</p><br/>
                    <p>Graphics (Gran Formato): Av Luis Orci 606, Choyal, 83130 Hermosillo, Son.
Tel. (662) 301 2565
</p><br/>
                    <p>Hermosillo, Son. Mxico</p><br/>   
                    
                </div>
            <div class="contact_right">
        <h2 class="contact_title welcome_text purple" >Mensaje</h2>
                <div className="line2"></div><br/>
                <form  /* action="enviar-prueba.php" method="post" */ className="formulario column column--50" onSubmit={sendEmail}>
            
                    <label for="" className="formulario__label">Name</label>
                    <div><input type="text" className="formulario__input-txt" name="nombre"/></div>
                    <label for="" class="formulario__label">Email</label>
                    <div><input type="text" className="formulario__input-txt" name="correo"/></div>
                    <label for="" className="formulario__label">Phone</label>
                    <div><input type="text" className="formulario__input-txt" name="telefono"/></div>
                        <label for="" className="formulario__label">Message</label>
                        <div><textarea name="mensaje" id="" cols="30" rows="10" className="formulario__textarea"> </textarea></div>
                            <input type="submit" className="formulario__btn" value="Enviar" onClick={confirmation}></input>
                           </form>
            </div>
            </div>
            <div className='margin'></div>
            <div className='hubicacion'>
               <a href='https://www.google.com.mx/maps/place/Sifra+Profesionales+en+Impresi%C3%B3n/@29.1452157,-111.0093704,17z/data=!3m1!4b1!4m5!3m4!1s0x86ce80d634949279:0x90e9b6e2f65fd016!8m2!3d29.1452089!4d-111.0071992'><img src={hubicacionImg}></img></a>
            </div>
    </div>
    )
}

export default Contacto
